/* Background */
.login-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: #007b8f;
}

/* Card */
.login-box {
  background: #fff;
  padding: 30px;
  width: 350px;
  border-radius: 10px;
  box-shadow: 0 6px 12px rgba(0,0,0,0.15);
  text-align: left;
}

/* Title */
.login-box h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

/* Labels */
.login-box label {
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 5px;
  display: block;
  color: #333;
}

/* Inputs */
.login-box input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 14px;
}

/* Buttons */
.btn-primary {
  width: 100%;
  padding: 10px;
  background: #007b8f;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  margin-bottom: 15px;
}
.btn-primary:hover { background: #006678; }

.btn-google {
  width: 100%;
  padding: 10px;
  background: #db4437;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  margin-bottom: 15px;
}
.btn-google:hover { background: #c1351d; }

.btn-warning {
  width: 100%;
  padding: 10px;
  background: #f4b400;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  margin-bottom: 10px;
}
.btn-warning:hover { background: #db9e00; }

.btn-success {
  width: 100%;
  padding: 10px;
  background: #0f9d58;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}
.btn-success:hover { background: #0c7a43; }

/* Divider */
.divider {
  text-align: center;
  margin: 15px 0;
  color: #888;
}

/* Links */
.login-links {
  text-align: center;
  margin-top: 15px;
}
.login-links a {
  color: #007b8f;
  text-decoration: none;
  font-size: 14px;
}
.login-links a:hover {
  text-decoration: underline;
}
